<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button @click="addDialogVisible=true" type="primary"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom',i1 === 0 ? 'bdtop' : '', 'venter']"
              v-for="(item1,i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'venter']"
                  v-for="(item2,i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="(item3,i3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showRolesEditDialog(scope.row.id)"
            ></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRolesById(scope.row.id)"></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showSetRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
    >
      <!--  -->
      <el-tree
        :data="rightslist"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showSetRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addFrom" :rules="addFromRules" ref="addFromRef" label-width="100px">
        <el-form-item label="" prop="rolename">
          <el-input v-model="addFrom.rolename"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addFrom.roledesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRoles"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editRolesDialogVisible" width="50%" @close="editRolesDialogClose">
      <el-form :model="editForm" :rules="editRolesFormRules" ref="editRolesFormRef" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRolesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRolesInfo()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      rolelist: [],
      //
      showSetRightDialogVisible: false,
      // 
      rightslist: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // Id
      defKeys: [],
      // id
      roleId: '',
      // 
      addDialogVisible: false,
      // 
      addFrom: { rolename: '', roledesc: '' },
      // 
      addFromRules: {
        rolename: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 4,
            max: 15,
            message: '3~25',
            trigger: 'blur'
          }
        ]
      },
      // 
      editForm: {},
      // 
      editRolesDialogVisible: false,
      editRolesFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 15,
            message: '2~25',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolelist = res.data
      console.log(this.rolelist)
    },
    // ID
    async removeRightById(role, rightId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) return this.$message.error('')
      role.children = res.data
    },
    // 
    async showSetRightDialog(role) {
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  data
      this.rightslist = res.data
      console.log(this.rightslist)
      // id
      this.getLeafKeys(role, this.defKeys)

      this.showSetRightDialogVisible = true
    },
    // iddefKeys
    getLeafKeys(node, arr) {
      if (!node.children) {
        // nodechildren
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setRightDialogClosed() {
      this.defKeys = []
    },
    // 
    async allotRights() {
      const keys = [
        this.$refs.treeRef.getCheckedKeys(),
        this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.getRolesList()
      this.showSetRightDialogVisible = false
    },
    // 
    addDialogClosed() {
      this.$refs.addFromRef.resetFields()
    },
    // 
    addRoles() {
      this.$refs.addFromRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('roles', {
          roleName: this.addFrom.rolename,
          roleDesc: this.addFrom.roledesc
        })
        if (res.meta.status !== 201) {
          this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible = false
        this.getRolesList()
      })
    },
    // 
    async showRolesEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      console.log(this.editForm)
      this.editRolesDialogVisible = true
    },
    // 
    editRolesInfo() {
      this.$refs.editRolesFormRef.validate(async valid => {
       if (!valid) return
      //  
      const {data:res} = await this.$http.put('roles/'+this.editForm.roleId,{roleName:this.editForm.roleName,roleDesc:this.editForm.roleDesc})
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.editRolesDialogVisible = false
      // 
      this.getRolesList()
      // 
      this.$message.success('!')
      })
    },
    // 
    editRolesDialogClose(){
      this.$refs.editRolesFormRef.resetFields()
    },
    // id
    async removeRolesById(id){
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      //  confirm
      // z cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
     const {data:res} = await this.$http.delete('roles/'+id)
     if (res.meta.status !== 200) {
       return this.$message.error('!')
     }
     this.$message.success('!')
     this.getRolesList()
    }
  }
}
</script>
<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.venter {
  display: flex;
  align-items: center;
}
</style>


